import React, { useState } from 'react'
import { getNiceSpan, getPointsFromSize, cmToPoints, translate } from '../lib/utils'
import ChartLines from './SVG/ChartLines'
import TextBox from './TextBox'
import Titles from './SVG/Titles'
import DateAxis from './SVG/DateAxis'
import styled from 'styled-components'

import Event from './SVG/Event'

const shiftToClosest = (compare, timestamps) => {
  if (timestamps.length === 0) { return compare }

  let smallest = 0
  let res = 0
  for (const timestamp of timestamps) {
    if (smallest === 0) {
      smallest = Math.abs(timestamp - compare)
    } else {
      if (smallest > Math.abs(timestamp - compare)) {
        smallest = Math.abs(timestamp - compare)
        res = timestamp
      }
    }
  }
  return res
}

const HoverCircle = styled.circle`
  :hover {
    fill: ${props => props.hoverColor};
  }
`

// const InfoPrompt = ({ pos, value }) => {
//   console.log(pos)
//   return (
//     <g transform={'translate( ' + pos[0] + ', ' + pos[1]}>
//       <text>{value}USD eesfiesf huiefs hufse huis ef efshuefs hu fesiuh efsihu efsihuefs ihu sefhiu</text>
//     </g>
//   )
// }

const Poster = ({ title = '', subTitle = '', font = "'Lora', serif", symbol = '', companyDescription = '', onCompanyDescriptionClick, exchangeName = '', market = '', color, fill = 'none', eventColor = color, showDate = true, startDate, endDate, size, stockData, events, style, onAddEvent, lineThickness }) => {
  // const [infoPrompt, setInfoPrompt] = useState(null)
  const width = React.useMemo(() => getPointsFromSize(size)[0] / 72 * 96, [size])
  const height = React.useMemo(() => getPointsFromSize(size)[1] / 72 * 96, [size])
  const [minValue, maxValue] = React.useMemo(() => {
    return getNiceSpan(stockData ?? [0, 0])
  })

  const chartY = 300
  const chartH = height - chartY - 265

  const chartX = 130
  const chartW = width - chartX - chartX

  const timestamps = React.useMemo(() => {
    if (!stockData) { return [] }
    return stockData.map(stock => stock.timestamp)
  }, [stockData])

  events = React.useMemo(() => {
    return events.map(event => {
      if (timestamps.includes(event.timeStamp)) {
        return event
      } else {
        event.timeStamp = shiftToClosest(event.timeStamp, timestamps)
        return event
      }
    })
  }, [events, stockData])

  const points = React.useMemo(() => {
    if (stockData == null) return []

    return stockData.map((stockObject, index, array) => {
      const value = stockObject.close
      const x = chartX + (index / (array.length - 1)) * chartW
      const y = chartY + chartH - (((value - minValue) / (maxValue - minValue)) * chartH)

      return `${x.toFixed(8)} ${y.toFixed(8)}`
    })
  }, [size, stockData, fill])

  const fillPoints = React.useMemo(() => {
    if (stockData == null) return []

    const xStart = chartX + 0
    const yStart = chartY + chartH
    const xEnd = chartX + ((stockData.length - 1) / (stockData.length - 1)) * chartW
    const yEnd = chartY + chartH
    return [xStart + ' ' + yStart, ...points, xEnd + ' ' + yEnd]
  }, [size, stockData, fill])

  const numberOfEventColumns = React.useMemo(() => {
    if (size.label === '30x40cm') {
      return 2
    } else if (size.label === '50x70cm') {
      return 4
    } else if (size.label === '70x100cm') {
      return 6
    } else {
      return 2
    }
  }, [size])

  const SlicedEvents = React.useMemo(() => {
    return events.slice(0, numberOfEventColumns * 5)
  }, [events, numberOfEventColumns])

  const freeColumns = React.useMemo(() => {
    const columns = Math.ceil(SlicedEvents.length / 5)
    return numberOfEventColumns - columns
  }, [SlicedEvents, numberOfEventColumns])

  const hovering = (e, pos, index) => {
    // const close = stockData[index].close
    // setInfoPrompt({ pos: pos, value: close })
  }

  // <svg id='mySVG' viewBox={`0 0 ${width} ${height}`} style={style}> // Viewbox causes issues with font-sizes!
  return (
    <svg id='mySVG' width={width} height={height} style={{ backgroundColor: 'white', display: 'block' }}>
      {/* <svg id='mySVG' width={width} height={height} style={{ backgroundColor: 'green', display: 'block' }}> */}
      <defs>
        <linearGradient id='gradient' x1='0%' y1='0%' x2='0%' y2='100%'>
          <stop offset='0%' stopColor={Array.isArray(fill) ? fill[0] : '#fff'} stopOpacity={1} />
          <stop offset='100%' stopColor={Array.isArray(fill) ? fill[1] : '#fff'} stopOpacity={0} />
        </linearGradient>
      </defs>

      {/* Borders */}
      <rect x='0' y='0' width={width} height={height} fill='white' />
      <rect x={cmToPoints(2.5)} y={cmToPoints(2.5)} width={width - cmToPoints(2.5) * 2} height={height - cmToPoints(2.5) * 2} fill='white' stroke='black' />
      <rect x={cmToPoints(3)} y={cmToPoints(3)} width={width - cmToPoints(6)} height={height - cmToPoints(6)} fill='white' stroke='black' strokeWidth='3' />

      {/* Titles */}
      <Titles width={width} height={height} title={title} subTitle={subTitle} font={font} showDate={showDate} startDate={startDate} endDate={endDate} />

      {/* CHART BG LINES */}
      <ChartLines font={font} chartX={chartX} chartY={chartY} chartW={chartW} chartH={chartH} minValue={minValue} maxValue={maxValue} />

      {/* CHART PATH */}
      <polyline fill='none' strokeWidth={lineThickness} stroke={color} points={points.join(' ')} />
      {fill !== 'none' &&
        <polyline fill='url(#gradient)' strokeWidth='0' stroke='none' points={fillPoints.join(' ')} />}

      {/* HOVER DOTS */}
      {points.map((point, i) => <HoverCircle onMouseEnter={(e) => hovering(e, [point.split(' ')[0], point.split(' ')[1]], i)} key={point} onClick={() => onAddEvent(stockData[i].timestamp)} cx={point.split(' ')[0]} cy={point.split(' ')[1]} r='5' stroke='none' fill='transparent' hoverColor={color} />)}

      {/* INFO PROMPT */}
      {/* {(infoPrompt && infoPrompt.pos) && <InfoPrompt pos={infoPrompt.pos} value={infoPrompt.value} />} */}
      {/* <InfoPrompt pos={infoPrompt.pos} value={infoPrompt.value} /> */}

      {/* EVENTS */}
      {events.map(event => <Event key={event.id} event={event} color={eventColor} stockData={stockData} points={points} />)}

      {SlicedEvents.map((event, i) => <text fontFamily={font} key={event.id + 'eventText'} x={chartX + chartW - (numberOfEventColumns - freeColumns) * 160 + Math.floor(i / 5) * 160} y={chartY + chartH + 60 + 16 * (i - Math.floor(i / 5) * 5)} fontSize={7}>{i + 1 + '. ' + event.text}</text>)}

      {/* Date axis */}
      {timestamps.length && <DateAxis font={font} startStamp={timestamps[0]} endStamp={timestamps[timestamps.length - 1]} chartW={chartW} chartX={chartX} y={chartY + chartH + 25} />}

      {/* COMPANY DESCRIPTION */}
      <TextBox onClick={onCompanyDescriptionClick} text={companyDescription} font={font} width={800} fontSize='7' lineSpacing='12' maxLines={false} x={chartX + 100} y={chartY + chartH + 60} />

      {/* <text x={0} y={chartY + chartH + 25}>I am a very long text that will be rendered incorrectly I presume. I am a very long text that will be rendered incorrectly I presume.</text> */}

      <g transform={translate(chartX, chartY + chartH + 60)}>
        <text fontSize='7' fontFamily={font} fontWeight='bolder' x={0} y={0}>{symbol}</text>
        {/* <text fontSize='7' fontFamily={font} fontWeight='bolder' x={0} y={15}>{exchangeName}</text> */}
        {/* <text fontSize='7' fontFamily={font} fontWeight='bolder' x={0} y={30}>{market}</text> */}
      </g>
    </svg>
  )
}

export default Poster
