import React from 'react'
import styled from 'styled-components'
import hexToRgba from 'hex-rgba'

const StyledButton = styled.div`
  background-color: ${props => props.enabled ? props.color : '#999'};
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  border-radius: 3px;
  cursor: ${props => props.enabled ? 'pointer' : 'initial'};
  transition: 200ms;

  :hover {
    background-color: ${props => props.enabled ? hexToRgba(props.color, 80) : '#999'};
  }
`

function Button ({ onClick, enabled = true, children, color = '#6F9DDF', style }) {
  return (
    <StyledButton style={style} color={color} onClick={enabled ? onClick : null} enabled={enabled}>
      {children}
    </StyledButton>
  )
}
export default Button
