import React, { useMemo } from 'react'
import InputHeadline from './InputHeadline'
import { useStickyState } from '../lib/utils'
import Spacer from './Spacer'
import HorizontalSelector from './HorizontalSelector'
import DatePicker from 'react-date-picker'

const year = new Date(0)
year.setFullYear(year.getFullYear() + 1)

const ranges = [
  { value: 'custom', label: 'custom' },
  { value: 'max', label: 'max' },
  { value: year.getTime() * 5, label: '5y' },
  { value: year.getTime(), label: '1y' },
  { value: year.getTime() / 2, label: '6mo' },
  { value: year.getTime() / 12, label: '1mo' }
]

const newISOFromISOMinusMS = (date, minus) => {
  return (new Date((new Date(date)).getTime() - minus)).toISOString()
}

console.log(ranges)

function DateSelector ({ startDate, endDate, onDates, color }) {
  const [range, setRange] = useStickyState(ranges[0], 'range')

  const changeStartDate = (date) => {
    onDates(date, endDate)
  }

  const changeEndDate = (date) => {
    if (range.value === 'custom' || range.value === 'max') {
      onDates(startDate, date)
    } else {
      onDates(newISOFromISOMinusMS(startDate, range.value), date)
    }
  }

  const changeRange = (val) => {
    if (val.value === 'max') {
      onDates((new Date(0)).toISOString(), (new Date()).toISOString())
    } else if (val.value === 'custom') {
      // Do nada
    } else {
      changeStartDate(newISOFromISOMinusMS(endDate, val.value))
    }
    setRange(val)
  }

  const startDateObj = useMemo(() => {
    return new Date(startDate)
  }, [startDate])

  const endDateObj = useMemo(() => {
    return new Date(endDate)
  }, [endDate])

  return (
    <>
      <InputHeadline>Select start date</InputHeadline>
      <DatePicker
        style={{ border: 'none !important' }}
        onChange={(date) => changeStartDate(date.toISOString())}
        value={startDateObj}
        disabled={range.value !== 'custom'}
      />
      <InputHeadline>Select end date</InputHeadline>
      <DatePicker
        style={{ border: 'none !important' }}
        onChange={(date) => changeEndDate(date.toISOString())}
        value={endDateObj}
        disabled={range.value === 'max'}
      />
      <Spacer />
      <HorizontalSelector value={range} options={ranges} onChange={changeRange} color={color} />
    </>
  )
}
export default DateSelector
