import React, { useEffect, useState } from 'react'
import styled from 'styled-components'

const Container = styled.div`
  display: flex;
  justify-content: space-between;
  border-radius: 5px;
  overflow: hidden;
  border: 1px solid ${props => props.color};
`

const Option = styled.div`
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8px 4px;
  background-color: ${props => props.selected ? props.color : '#fff'};
  color: ${props => props.selected ? '#fff' : '#666'};
  transition: 200ms;
  cursor: pointer;

  :hover {
    background-color: ${props => props.color};
    color: #fff;
  }
`

function HorizontalSelector ({ value, onChange, options, color = '#297BE6' }) {
  const [currentValue, setCurrentValue] = useState(value)

  const elements = options.map(option => <Option key={option.label + option.value} onClick={(e) => click(e, option)} selected={currentValue.value === option.value} color={color}>{option.label}</Option>)

  const click = (e, option) => {
    setCurrentValue({ ...option })
  }

  useEffect(() => {
    onChange(currentValue)
    console.log('sent back!')
  }, [currentValue])

  return (
    <Container className='horizontalSelector' color={color}>
      {elements}
    </Container>
  )
}
export default HorizontalSelector
