import React from 'react'

function ChartLine ({ relativeYPos, chartX, chartW, chartY, chartH, chartLineColor, font, textColor, fontSize, minValue, ΔValue }) {
  const textValue = Math.round(minValue + ΔValue * Math.abs(relativeYPos - 1)) // Account for precision errors!

  return (
    <g>
      <line x1={chartX} x2={chartX + chartW} y1={chartY + (chartH * relativeYPos)} y2={chartY + (chartH * relativeYPos)} stroke={chartLineColor} />
      <text fontFamily={font} fill={textColor} fontSize={fontSize} x={chartX - 5} textAnchor='end' alignmentBaseline='middle' y={chartY + (chartH * relativeYPos)}>{textValue}</text>
    </g>
  )
}

function ChartLines ({ font, chartX, chartY, chartW, chartH, minValue, maxValue, textColor = '#777', fontSize = '7' }) {
  const ΔValue = maxValue - minValue
  const chartLineColor = '#ddd'

  let numberOfChartLines = 5
  // Check the best looking numbers first hence wierd order!
  if (!(ΔValue % 5)) {
    numberOfChartLines = 4
  } else if (!(ΔValue % 6)) {
    numberOfChartLines = 5
  } else if (!(ΔValue % 7)) {
    numberOfChartLines = 6
  } else if (!(ΔValue % 4)) {
    numberOfChartLines = 3
  }

  const iterator = Array.from(Array(numberOfChartLines).keys())
  const Lines = iterator.map((item, index) => <ChartLine key={index + 'chartLine'} relativeYPos={index / (numberOfChartLines - 1)} chartX={chartX} chartW={chartW} chartY={chartY} chartH={chartH} chartLineColor={chartLineColor} font={font} textColor={textColor} fontSize={fontSize} minValue={minValue} ΔValue={ΔValue} />)

  return (
    <g>
      {Lines}
    </g>
  )
}
export default ChartLines
