import React from 'react'
import Popup from 'react-animated-popup'
import styled from 'styled-components'
import Button from './Button'

const Text = styled.p`

`

const ButtonContainer = styled.div`
  display: flex;
  flex-direction: row;

  div {

  }
`

const Spacer = styled.div`
  width: 20px;
  flex-grow: 0;
`

function YesNoPrompt ({ visible, children, color, onYes, onNo }) {
  return (
    <Popup onClose={onNo} visible={visible}>
      <Text>{children}</Text>
      <ButtonContainer>
        <Button onClick={onNo} style={{ flex: 1 }} color={color}>No</Button>
        <Spacer />
        <Button onClick={onYes} style={{ flex: 1 }} color={color}>Yes</Button>
      </ButtonContainer>
    </Popup>
  )
}
export default YesNoPrompt
