import React, { useState, useEffect, useMemo } from 'react'
import styled from 'styled-components'
import Select from 'react-select'
import { getChart, getProfile } from './lib/api'
import InputHeadline from './components/InputHeadline'
import Poster from './components/Poster'
import StockSelect from './components/StockSelect'
import { dateToString, exportPDF, getPointsFromSize, useStickyState, getFontNameFromURL } from './lib/utils'
import Input from './components/Input'
import { TwitterPicker } from 'react-color'
import Checkbox from './components/Checkbox'
import Button from './components/Button'
import AutoSize from './components/AutoSize'
import Swatch from './components/Swatch'
import Slider from 'react-input-slider'
import InputPopup from './components/InputPopup'
import EventEditor from './components/EventEditor'
import { v4 as uuidv4 } from 'uuid'
import RoundedBlock from './components/RoundedBlock'
import Spacer from './components/Spacer'
import DateSelector from './components/DateSelector'
import hexToRgba from 'hex-rgba'
import YesNoPrompt from './components/YesNoPrompt'
import Popup from 'react-animated-popup'
import TextEditor from './components/TextEditor'
import CartItem from './components/CartItem'

const dev = !!window.location.href.includes('localhost:3000')
// const dev = false

const themeColor = '#272f44'

const sizes = [
  { value: { size: [40, 30], unit: 'cm' }, label: '30x40cm' },
  { value: { size: [70, 50], unit: 'cm' }, label: '50x70cm' },
  { value: { size: [100, 70], unit: 'cm' }, label: '70x100cm' },
  { value: { size: [17, 11], unit: 'inches' }, label: '11x17"' },
  { value: { size: [24, 18], unit: 'inches' }, label: '18x24"' },
  { value: { size: [36, 24], unit: 'inches' }, label: '24x36"' }
]

const fonts = [
  { value: 'myLora', label: 'Classic', url: '/smallimprint-stock-chart-demo/Lora.ttf' },
  { value: 'myOpen Sans', label: 'Modern', url: '/smallimprint-stock-chart-demo/OpenSans-Regular.ttf' }
]

export const getFonts = () => { return fonts }

const fontsCSSCode = fonts.map((font) => {
  return `
  @font-face {
    font-family: ${font.value};
    src: url(${font.url}) format('truetype');
  }
  `
})

const Container = styled.div`
  display: flex;
  flex-direction: row;
  min-height: calc(100vh - 130px);
  width: 100vw;
  background-color: #f4f4f4;

  .react-date-picker__wrapper {
    border: none;
  }

  .react-date-picker__wrapper svg {
    width: 12px;
  }

  @media screen and (max-width: 600px) {
    flex-direction: column;
  }

  ${fontsCSSCode}
`

const SideBar = styled.div`
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  width: 350px;
  padding: 20px;
  max-height: calc(100vh - 40px - 90px);
  overflow: scroll;

  @media screen and (max-width: 600px) {
    width: auto;
    overflow: visible;
  }

  * {
    flex-shrink: 0;
  }
`

const PosterContainer = styled.div`
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: 1px;
  padding: 20px;

  @media screen and (max-width: 600px) {
    flex-grow: 0;
  }
`

const Disclamer = styled.p`
  color: #999;
  font-size: 12px;
`

const getStockData = async (dev, symbol, startDate, endDate) => {
  const data = dev && window.localStorage.getItem('apple') ? JSON.parse(window.localStorage.getItem('apple')) : await getChart(symbol, startDate, endDate)
  // const data = await getChart(symbol, startDate, endDate)
  window.localStorage.setItem('apple', JSON.stringify(data))
  // const data = JSON.parse(window.localStorage.getItem('apple'))
  // console.log(data)
  return (data)
}

const getProfileData = async (dev, symbol) => {
  const data = dev && window.localStorage.getItem('appleProfile') ? JSON.parse(window.localStorage.getItem('appleProfile')) : await getProfile(symbol)
  // const data = await getProfile(symbol)
  // console.log('here is the data')
  // console.log(data)
  window.localStorage.setItem('appleProfile', JSON.stringify(data))
  // const data = JSON.parse(window.localStorage.getItem('appleProfile'))

  // console.log(data)

  return (data)
}

// stupid fix
const PopupWidener = styled.div`
  div>div {
    max-width: 80vw;
  }
`

const aYearAgo = new Date((new Date()).setFullYear((new Date()).getFullYear() - 1))

function App () {
  const [stockSymbol, setStockSymbol] = useStickyState({ value: 'AAPL', label: 'Apple Inc' }, 'stockSymbol')
  const [title, setTitle] = useStickyState('Your title', 'title')
  const [subTitle, setSubtitle] = useStickyState('Your subtitle', 'subTitle')
  const [companyDescription, setCompanyDescription] = useState('')
  const [exchangeName, setExchangeName] = useState('')
  const [market, setMarket] = useState('')
  const [startDate, setStartDate] = useStickyState(aYearAgo.toISOString(), 'startDate')
  const [endDate, setEndDate] = useStickyState(new Date().toISOString(), 'endDate')
  const [stockData, setStockData] = useState([])
  const [size, setSize] = useStickyState(sizes[0], 'size')
  const [font, setFont] = useStickyState(fonts[0], 'font')
  const [lineThickness, setLineThickness] = useStickyState(1, 'lineThickness')
  const [color, setColor] = useStickyState('#1273DE', 'color')
  const [eventColor, setEventColor] = useStickyState(true, 'eventColor')
  const [fill, setFill] = useStickyState(true, 'showFill')
  const [showDate, setShowDate] = useStickyState(true, 'showDate')
  const [currentlyAddingEvent, setCurrentlyAddingEvent] = useState(false)
  const [events, setEvents] = useStickyState([], 'events') // [{timestamp: 1, text: 'foo'}]
  const [appHasLoaded, setAppHasLoaded] = useState(false)
  const [showClearAllEventsPopup, setShowClearAllEventsPopup] = useState(false)
  const [showCompanyDescriptionPopup, setShowCompanyDescriptionPopup] = useState(false)
  const [price, setPrice] = useState()

  const [devMode, setDevMode] = useStickyState(dev, 'devMode')

  // Uncomment to use dynamic theme color
  // themeColor = useMemo(() => {
  //   return color
  // }, [color])

  const fillColors = useMemo(() => {
    const startColor = hexToRgba(color, 50)
    return [startColor, color]
  }, [color])

  useMemo(() => {
    setShowClearAllEventsPopup(!!((appHasLoaded && events.length))) // Don't show prompt on first app load
  }, [stockSymbol])

  useEffect(() => {
    setAppHasLoaded(true)
  }, [])

  useEffect(() => {
    if (!stockSymbol) { return }
    const fetchStock = async () => {
      console.log('fetching stock')
      const stock = await getStockData(devMode, stockSymbol.value, startDate, endDate)
      setStockData(stock)

      // use this line to debug events
      if (devMode) {
        setEvents([{ timeStamp: 1599053400, text: 'Fam pork', index: 1, id: 'foo' }, { timeStamp: 1599658200, text: 'Vinyl pinterest', index: 2, id: 'foo2' }, { timeStamp: 1600176600, text: 'XOXO schlitz', index: 3, id: 'foo3' }, { timeStamp: 1600435800, text: 'Vape shaman', index: 4, id: 'foo4' }, { timeStamp: 1601559000, text: 'Poke synth', index: 5, id: 'foo5' }, { timeStamp: 1602250200, text: 'Pour-over', index: 6, id: 'foo6' }, { timeStamp: 1602595800, text: 'Next level', index: 7, id: 'foo7' }, { timeStamp: 1603805400, text: 'taiyaki truffaut', index: 8, id: 'foo8' }, { timeStamp: 1603891800, text: 'Bushwick green', index: 9, id: 'foo9' }, { timeStamp: 1603978200, text: 'Tumblr flexitarian ', index: 10, id: 'foo10' }])
      }
    }
    fetchStock()
  }, [stockSymbol, startDate, endDate])

  useEffect(() => {
    if (!stockSymbol) { return }
    const fetch = async () => {
      console.log('fetching profile')
      const profile = await getProfileData(devMode, stockSymbol.value)
      setExchangeName(profile.price.exchangeName)
      setMarket(profile.quoteType.market)
      if (profile.symbol === '^IXIC') { // Missing business summary, todo make json object for these
        profile.assetProfile = { longBusinessSummary: "The NASDAQ (National Association of Securities Dealers Automated Quotations) is an electronic stock exchange with more than 3,300 company listings. It was founded in 1971 by the National Association of Securities Dealers (NASD), now known as the Financial Industry Regulatory Authority. On February 8, 1971, the Nasdaq stock market began operations as the world's first electronic stock market." }
      } else if (profile.symbol === '^DJI') {
        profile.assetProfile = { longBusinessSummary: 'The Dow Jones Industrial Average (DJIA), Dow Jones, is a stock market index that measures the stock performance of 30 large companies listed on stock exchanges in the United States. Although it is one of the most commonly followed equity indices, many consider the Dow to be an inadequate representation of the overall U.S. stock market compared to broader market indices such as the S&P 500 Index or Russell 3000 because it only includes 30 large cap companies, is not weighted by market capitalization, and does not use a weighted arithmetic mean. First calculated on May 26, 1896,the index is the second-oldest among the U.S. market indices (after the Dow Jones Transportation Average). It was created by Charles Dow, the editor of The Wall Street Journal and the co-founder of Dow Jones & Company, and named after him and his business associate, statistician Edward Jones.' }
      } else if (profile.symbol === '^GSPC') {
        profile.assetProfile = { longBusinessSummary: 'The S&P 500 is a stock market index that measures the stock performance of 500 large companies listed on stock exchanges in the United States. It is one of the most commonly followed equity indices. The S&P 500 Index is a market-capitalization-weighted index of the 500 largest U.S. publicly traded companies. The S&P is a float-weighted index, meaning company market capitalizations are adjusted by the number of shares available for public trading. 10 largest companies in the index account for 26% of the market capitalization of the index. The components that have increased their dividends in 25 consecutive years are known as the S&P 500 Dividend Aristocrats. The index value is updated every 15 seconds, or 1,559 times per trading day. The S&P 500 is maintained by S&P Dow Jones Indices, a joint venture majority-owned by S&P Global, and its components are selected by a committee.' }
      }
      setCompanyDescription(profile.assetProfile ? profile.assetProfile.longBusinessSummary : '')
    }
    fetch()
  }, [stockSymbol])

  const onNewStockSymbol = (symbol) => {
    setStockSymbol(symbol)
    setTitle('')
    setSubtitle('')
  }

  const addEvent = (eventText, timeStamp = currentlyAddingEvent) => {
    const newEvents = [...events, { timeStamp: timeStamp, text: eventText, index: 0, id: uuidv4() }].sort((a, b) => a.timeStamp > b.timeStamp).map((event, i) => ({ ...event, index: i + 1 }))

    setEvents(newEvents)
    console.log(newEvents)
    setCurrentlyAddingEvent(false)
  }

  const deleteEvent = (event) => {
    setEvents(events.filter(e => e.timeStamp !== event.timeStamp))
  }

  const setBothDates = (start, end) => {
    setStartDate(start)
    setEndDate(end)
  }

  const clearEvents = () => {
    setEvents([])
    setShowClearAllEventsPopup(false)
  }

  const updateCompanyDescription = (text) => {
    setCompanyDescription(text)
    setShowCompanyDescriptionPopup(false)
  }

  const getPrice = async (size) => {
    const queryString = size.label.replace('cm', '').replace('"', '')
    const price = await window.getPrice(queryString)
    return price
  }

  const buy = async () => {
    const pdf = await exportPDF(getPointsFromSize(size), devMode)
    const chartData = {
      title: title,
      subtitle: subTitle,
      symbol: stockSymbol.value,
      startDate: startDate,
      endDate: endDate
    }
    await window.sendPDF(pdf, chartData)
    if (!devMode) {
      window.location.href = 'https://thefinanceposter.com/cart/'
    }
  }

  useEffect(() => {
    const fetch = async () => {
      setPrice(await getPrice(size))
    }
    fetch()
  }, [size])

  return (
    <Container>
      {/* <iframe id='pdf-file' />  Iframe used for pdf export debugging */}
      <PosterContainer>
        <AutoSize>
          <Poster title={title} subTitle={subTitle} font={font.value} symbol={stockSymbol.value} companyDescription={companyDescription} onCompanyDescriptionClick={() => setShowCompanyDescriptionPopup(true)} exchangeName={exchangeName} market={market} startDate={startDate} endDate={endDate} color={color} fill={fill ? fillColors : 'none'} eventColor={eventColor === '#000' ? eventColor : color} showDate={showDate} size={size} stockData={stockData} events={events} onAddEvent={setCurrentlyAddingEvent} lineThickness={lineThickness} />
        </AutoSize>
        <PopupWidener>
          <Popup visible={showCompanyDescriptionPopup} onClose={() => setShowCompanyDescriptionPopup(false)}>
            <TextEditor color={themeColor} onCancel={() => setShowCompanyDescriptionPopup(false)} onText={updateCompanyDescription} maxWords={1740}>{companyDescription}</TextEditor>
          </Popup>
        </PopupWidener>
      </PosterContainer>

      <SideBar>
        {dev && <Checkbox value={devMode} onChange={setDevMode}>Dev mode</Checkbox>}

        <RoundedBlock title='Select' expandable startOpen>
          <StockSelect value={stockSymbol} onStockSelect={onNewStockSymbol} />
          <DateSelector color={themeColor} startDate={startDate} endDate={endDate} onDates={setBothDates} />
        </RoundedBlock>
        <Spacer height={20} />
        <RoundedBlock title='Design' expandable>
          <Input value={title} onChange={(e) => setTitle(e.target.value)} placeholder='Title' />
          <Input value={subTitle} onChange={(e) => setSubtitle(e.target.value)} placeholder='Subtitle' />
          <InputHeadline>Size</InputHeadline>
          <Select
            value={size}
            onChange={setSize}
            options={sizes}
          />

          <InputHeadline>Font</InputHeadline>
          <Select
            value={font}
            onChange={setFont}
            options={fonts}
          />

          <InputHeadline>Stroke thickness: {lineThickness}px</InputHeadline>
          <Spacer height={3} />
          <Slider
            styles={{ active: { backgroundColor: themeColor }, track: { height: 5, width: '100%' } }}
            color={themeColor}
            axis='x'
            x={lineThickness * 10}
            onChange={({ x }) => setLineThickness(x / 10)}
          />

          <Swatch colors={color} />
          <TwitterPicker
            colors={['#000', '#B80000', '#DB3E00', '#FCCB00', '#008B02', '#006B76', '#1273DE', '#004DCF', '#5300EB', '#EB9694', '#FAD0C3', '#FEF3BD', '#C1E1C5', '#BEDADC', '#C4DEF6', '#BED3F3', '#D4C4FB']}
            width='100%'
            color={color}
            disableAlpha
            onChange={(color) => setColor(color.hex)}
          />

          <Spacer />
          <Checkbox value={fill} onChange={() => setFill(!fill)}>Filled</Checkbox>
          <Checkbox value={eventColor === '#000'} onChange={() => setEventColor(eventColor === '#000' ? color : '#000')}>Black event indicators</Checkbox>
          {/* <Checkbox value={showDate} onChange={() => setShowDate(!showDate)}>Show date</Checkbox> */}

        </RoundedBlock>
        <Spacer height={20} />
        <RoundedBlock title='Events' expandable>
          <Disclamer>(This is optional)</Disclamer>
          <EventEditor minDate={stockData.length ? new Date(stockData[0].timestamp * 1000) : null} maxDate={stockData.length ? new Date(stockData[stockData.length - 1].timestamp * 1000) : null} events={events} onAdd={(event) => addEvent(event.text, event.timeStamp)} onDelete={deleteEvent} color={themeColor} />
        </RoundedBlock>
        <YesNoPrompt color={themeColor} visible={showClearAllEventsPopup} onYes={clearEvents} onNo={() => setShowClearAllEventsPopup(false)}>Would you like to clear your events?</YesNoPrompt>

        <Spacer height={20} />

        <RoundedBlock title='Purchase'>
          <CartItem price={price && price.value + ' ' + price.currency}>Chart {size.label}</CartItem>
          <Button onClick={buy} color={themeColor}>Add to cart</Button>
        </RoundedBlock>

      </SideBar>

      <InputPopup maxCharacters={46} visible={currentlyAddingEvent} onClose={() => setCurrentlyAddingEvent(false)} title={'Create event for ' + dateToString(new Date(currentlyAddingEvent * 1000))} onSubmit={addEvent} />
    </Container>
  )
}

export default App
