import React from 'react'
import textWrap from 'svg-text-wrap'
import styled from 'styled-components'

const Rect = styled.rect`
  :hover {
    cursor: ${props => props.clickable ? 'pointer' : 'initial'};
  }
`

function TextBox ({ text = '', font, x = 0, y = 0, maxLines = null, width = 200, fontSize, lineSpacing = 20, onClick }) {
  let lines = textWrap(text, width, { 'font-size': fontSize })
  if (maxLines) {
    lines = lines.slice(0, maxLines)
  }

  return (
    <g onClick={onClick} transform={'translate(' + x + ', ' + y + ')'}>
      {lines.map((line, i) => <text x={0} y={i * lineSpacing} key={line + i} fontSize={fontSize} fontFamily={font}>{line}</text>)}
      <Rect clickable={!!onClick} y={-fontSize} width={width} height={lines.length * lineSpacing > 89 ? lines.length * lineSpacing : 89} fill='transparent' />{/* Transparent */}
    </g>
  )
}
export default TextBox
