import React, { useState } from 'react'
import styled from 'styled-components'
import Button from './Button'
import Spacer from './Spacer'

const Title = styled.h1`
  margin-top: 0;
`

const TextArea = styled.textarea`
  height: 200px;
  max-width: 600px;
  width: calc(80vw - 40px);
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-family: sans-serif;
`

const ButtonContainer = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
`

const ButtonSpacer = styled.div`
  width: 10px;
`

const MaxWordText = styled.div`
  color: ${props => props.overLimit ? 'red' : '#aaa'};
  margin-top: 2px;
  font-size: 10px;
`

const buttonStyle = {
  flex: 1,
  maxWidth: 100
}

function TextEditor ({ children, onCancel, onText, maxWords, color }) {
  const [text, setText] = useState(children)

  const maxWordsText = text.length + ' / ' + maxWords

  return (
    <>
      <Title>Edit text</Title>
      <TextArea value={text} onChange={(e) => setText(e.target.value)} />
      {maxWords && <MaxWordText overLimit={text.length > maxWords}>{maxWordsText}</MaxWordText>}
      <Spacer height={20} />
      <ButtonContainer>
        <Button color={color} style={buttonStyle} onClick={() => onCancel(text)}>Cancel</Button>
        <ButtonSpacer />
        <Button color={color} style={buttonStyle} enabled={text.length < maxWords} onClick={() => onText(text)}>Save</Button>
      </ButtonContainer>
    </>
  )
}
export default TextEditor
