import React, { useEffect, useState, useRef, useMemo } from 'react'
import styled from 'styled-components'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faAngleRight } from '@fortawesome/free-solid-svg-icons'

const retractedHeight = 21

const Container = styled.div`
  position: relative;
  padding: 20px;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  height: ${props => props.height}px;
  overflow: hidden;
  transition: 200ms;
`

const Content = styled.div`
  display: flex;
  flex-direction: column;
`

const Hitbox = styled.div`
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: ${retractedHeight + 20 * 2}px;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  cursor: pointer;
  border-radius: 10px;
  transition: 200ms;
`

const Arrow = styled.div`
  transition: 200ms;
  transform: rotate(${props => props.expanded ? '270' : '90'}deg);
  padding: 10px;
  margin-right: 10px;
  user-select: none;
`

const Title = styled.h2`
  margin: 0;
  margin-bottom: 24px;
  font-size: 18px;
  display: flex;
  justify-content: space-between;
  align-items: center;
`

const iconStyle = {
  padding: '0 6'
}

function RoundedBlock ({ title, children, expandable = false, startOpen = false }) {
  const [currentHeight, setCurrentHeight] = useState(expandable && !startOpen ? retractedHeight : 'auto')
  const [targetHeight, setTargetHeight] = useState()
  const ContainerRef = useRef(null)

  const expanded = useMemo(() => {
    return currentHeight !== retractedHeight
  }, [currentHeight])

  useEffect(() => {
    if (ContainerRef.current) {
      setTargetHeight(ContainerRef.current.scrollHeight)
      if (expanded) {
        setCurrentHeight(ContainerRef.current.scrollHeight)
      }
    }
  }, [children])

  useEffect(() => {
    if (startOpen) {
      setCurrentHeight(ContainerRef.current.scrollHeight)
    }
  }, [])

  const handleToggle = (e) => {
    setCurrentHeight(expanded ? retractedHeight : targetHeight)
  }

  return (
    <Container height={currentHeight}>
      {expandable &&
        <Hitbox onClick={handleToggle}>
          <Arrow expanded={expanded}>
            <FontAwesomeIcon size='sm' style={iconStyle} icon={faAngleRight} />
          </Arrow>
        </Hitbox>}
      <Content ref={ContainerRef}>
        {title &&
          <Title>
            {title}
          </Title>}
        {children}
      </Content>
    </Container>
  )
}
export default RoundedBlock
