import React from 'react'
import PDFDocument from '@react-pdf/pdfkit'
import SVGtoPDF from 'svg-to-pdfkit'
import blobStream from 'blob-stream'
import { getFonts } from '../App'

export function useStickyState (defaultValue, key) {
  const [value, setValue] = React.useState(() => {
    const stickyValue = window.localStorage.getItem(key)
    return stickyValue !== null
      ? JSON.parse(stickyValue)
      : defaultValue
  })
  React.useEffect(() => {
    window.localStorage.setItem(key, JSON.stringify(value))
  }, [key, value])
  return [value, setValue]
}

const getOneLowerPotensThanNumber = (num) => {
  const numString = String(Math.round(num))
  const potens = numString.length
  const res = Math.pow(10, potens - 2)
  if (res === 1) {
    return 10 // 1 never looks good...
  } else {
    return Math.pow(10, potens - 2)
  }
}

// Yeah this function is pretty ugly... Figure out a better way to do it perhaps...
export const getNiceSpan = (stockData) => {
  const values = stockData.map(stock => stock.close)
  const max = Math.ceil(values.reduce(function (a, b) {
    return Math.max(a, b)
  }, 0))

  const min = Math.floor(values.reduce(function (a, b) {
    return Math.min(a, b)
  }, max))

  const toAdd = getOneLowerPotensThanNumber(max)

  let res = Math.ceil((max) / toAdd) * toAdd

  res = Math.round(res / 5) * 5

  let minRes = 0
  if (max * 0.2 > min) {
    // lower than 20% no point in clipping graph
    minRes = 0
  } else {
    const toAdd = getOneLowerPotensThanNumber(min)
    minRes = Math.floor((min) / toAdd) * toAdd
  }

  return [minRes, res]
}

export const pad = (inString) => {
  const string = '0' + inString
  return string.slice(string.length - 2, string.length)
}

export const dateToString = (date) => {
  const res = pad(date.getMonth() + 1) + '/' + pad(date.getDate()) + '/' + date.getFullYear()
  return res
}

export const downloadURI = (uri, name) => {
  const link = document.createElement('a')
  link.download = name
  link.href = uri
  document.body.appendChild(link)
  link.click()
  document.body.removeChild(link)
}

export const inchesToPoints = (inches) => {
  return inches * 72
}

export const cmToPoints = (cm) => {
  return inchesToPoints(cm * 0.393700787)
}

export const getPointsFromSize = (size) => {
  if (size.value.unit === 'cm') {
    return size.value.size.map(size => cmToPoints(size))
  } else if (size.value.unit === 'inches') {
    return size.value.size.map(size => inchesToPoints(size))
  }
}

export const svgToPDFURL = async (size) => {
  let url = ''
  const doc = new PDFDocument({
    size: 'A4',
    margins: { // by default, all are 72
      top: 0,
      bottom: 0,
      left: 0,
      right: 0
    }
    // layout: 'landscape' // default is portrait
  })
  const svg = document.getElementById('mySVG')

  SVGtoPDF(doc, svg, 0, 0, { width: 200 })
  doc.end()
  const stream = await doc.pipe(blobStream())
  await stream.on('finish', function () {
    // const iframe = document.getElementById('pdf-file')
    // iframe.src = stream.toBlobURL('application/pdf')
    // console.log(stream.toBlobURL('application/pdf'))
    url = URL.createObjectURL(stream.toBlob()) // Possibly a way to use PDFViewer
    // console.log(url)

    downloadURI(stream.toBlobURL('application/pdf'), 'mychartv2.pdf')
  })
  console.log(url)
  return url
}

export const getFontNameFromURL = (url) => {
  return 'my' + (url.split('/')[url.split('/').length - 1]).split('.')[0]
}

export const loadFonts = async (doc) => {
  const fonts = getFonts()
  return new Promise((resolve, reject) => {
    let loaded = 0
    for (const font of fonts) {
    // LOAD FONTS
      const xhr = new window.XMLHttpRequest()
      xhr.open('GET', font.url, true)
      xhr.responseType = 'arraybuffer'

      xhr.onload = function (e) {
        if (this.status === 200) {
          doc.registerFont(font.value, xhr.response)
          loaded++
          if (loaded === fonts.length) {
            resolve('Success!') // Yay! Everything went well!
          }
        }
      }
      xhr.send()
    }
  })
}

export const exportPDF = async (size, saveLocal) => {
  return new Promise((resolve) => {
    const doc = new PDFDocument({
      size: size,
      margins: { // by default, all are 72
        top: 0,
        bottom: 0,
        left: 0,
        right: 0
      }
    // layout: 'landscape' // default is portrait
    })

    loadFonts(doc).then(async () => {
      const svg = document.getElementById('mySVG')
      SVGtoPDF(doc, svg, 0, 0, { width: size[0] })
      doc.end()
      const stream = doc.pipe(blobStream())
      stream.on('finish', function () {
        // A way to show live pdf!
        // const iframe = document.getElementById('pdf-file')
        // iframe.src = stream.toBlobURL('application/pdf')
        // console.log(stream.toBlobURL('application/pdf'))
        // console.log(URL.createObjectURL(stream.toBlob())) // Possibly a way to use PDFViewer
        if (saveLocal) {
          downloadURI(stream.toBlobURL('application/pdf'), 'mychart.pdf')
        }
        // return stream.toBlob()
        resolve(stream.toBlob('application/pdf'))
      })
    })
  })
}

export const getIndexFromTimeStamp = (stockData, inStamp) => {
  if (stockData.length === 0) { return -1 }
  for (const [i, stock] of stockData.entries()) {
    if (inStamp === stock.timestamp) {
      return i
    }
  }
  return -1
}

export const translate = (x, y) => {
  return 'translate(' + x + ',' + y + ')'
}

export const scale = (scale, width) => {
  // return ' scale(' + scale + ')'
  const cx = width / 2
  const cy = 0
  return 'matrix(' + scale + ', 0, 0, ' + scale + ', ' + (cx - scale * cx) + ', ' + (cy - scale * cy) + ')'
}
